.projects {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.projects-parallax {
  position: absolute;
  top: 28%;
  left: 0;
  width: 100%;
  text-align: center;
}

.content {
  width: 100%;
  height: 100%;
}

@media all and (max-width:800px) {
  .content {
    display: flex;
    flex-direction: column;
  }

  h1 {
    font-size: 80px !important;
  }

  /* bg */
  .moonlight {
    width: 180%;
    height: 180%;
    left: -45% !important;
    top: -66% !important;
  }

  .stars {
    left: -45% !important;
    top: -21% !important;
  }
  /* landingpage */
  .right-box > svg{
    width: 80% !important;
  }

  .moon {
    left: 4.5% !important;
    top: -16% !important;
  }

  .mini-moon {
    top: 3% !important;
  }

  .cloud1 {
    left: -36% !important;
    top: 21% !important;
  }

  .cloud2 {
    left: 53% !important;
    top: 7% !important;
  }

  .cloud3 {
    left: 33% !important;
    top: 28% !important;
  }

  .bcloud1 {
    left: 39% !important;
    top: -3% !important;
  }

  .bcloud2 {
    left: -35% !important;
    top: 1% !important;
  }

  .left-box {
    width: 90% !important;
    left: 0 !important;
    margin-left: 10%;
    top: 46% !important;
  }

  .title {
    font-size: 48px !important;
  }

  .span-box > span {
    height: 2px;
  }

  .span-box > .span10 {
    margin-bottom: 17px !important;
        width: 120px !important;
  }

  .span-box > .span20 {
    margin-left: 45px !important;
        width: 120px !important;
  }

  .intro {
    font-size: 16px;
  }

/* projects */

  .left-box1 {
    width: 100% !important;
    margin-left: 5% !important;
    padding-top: 0 !important;
    margin-top: 6% !important;
  }

  .left-box1 > h1 {
    font-size: 50px !important;
  }

  .right-box1 {
    width: 100% !important;
    order: -1;
    margin-top: 30% !important;
    height: 70% !important;
  }
  .daimo-image {
    width: 90% !important;
    margin-left: 5% !important;
    margin-right: 5% !important
  }

  .span-box > span {
    width: 200px !important;
  }
  .span1 {
    margin-bottom: 50px !important;
  }

  .span2 {
    margin-left: 120px !important;
  }

  .daimo-description {
    width: 90%;
    font-size: 15px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  .resume {
    width: 50%;
    margin-left: 20%;
    font-size: 13px;
    margin-top: 3%;
  }

  /* footer */

  .daimo-description > h1 {
    font-size: 20px !important;
    margin-bottom: 13px !important;
  }

  .email {
    margin: 0 !important;
  }
}




.left-box1 {
  float: left;
  width: 22%;
  height: 100%;
  display:inline-block;
  margin-left: 13%;
  padding-top: 5%;
}

.daimo-description {
  color: white;
  margin-bottom: 5%;
  margin-top: 5%;
}

.fifa-title {
  font-size: 7rem !important;
}


.left-box1 > h1 {
  color: white;
  font-family: Futura-Med;
  font-size: 8rem;
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 1.6rem;
  font-weight: normal;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}

.right-box1 {
  display:inline-block;
  height: 100%;
  width: 64%;
  margin-top: 6%;
}

.daimo-image {
  margin-left: 10%;
  width: 80%;
}
