.footer {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.daimo-description > h1 {
  font-size: 2rem;
  font-weight: normal;
  margin-bottom:40px;
}


.right-box3 {
  margin-top: 7%;
  height: 100%;
  width: 80%;
  position: relative;
  left: 40%;
}


.icons > svg {
  width: 40px;
  margin-right: 50px;
}

.icons > svg:hover {
  fill:  #73ff59;
}

.left-box3 {
  width: 26%;
  margin-top:6%;
  left: 13%;
  position: absolute;
}

.left-box3 > h1 {
  color: white;
  font-family: Futura-Med;
  font-size: 7rem;
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 1.6rem;
  font-weight: normal;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}
