.landingpage {
  height: 100%;
  width: 100%;
  overflow: hidden;
}


.left-box {
  width: 50%;
  position: absolute;
  left: 13%;
  top: 16%;
}

.left-box > .title {
  color: white;
  font-family: Futura-Med;
  font-size: 8rem;
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 1.6rem;
  font-weight: normal;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}

.left-box > .span-box{
  width: 200px;
  height: 30px;
}

.resume {
  width: 200px;
  height: 50px;
  border-radius: 5px;
  background-color: #2bc90e;
  color: white;
  border: 0;
  font-size: 0.8rem;
}

.resume:hover {
  background-color: #73ff59;
}

.intro {
    color: #fff;
    font-size: 2.3rem;
    line-height: 0.7;
    letter-spacing: 0.1em;
}

 .span-box > span {
  display: block;
  height: 5px;
  width: 90px;
  background-color: #73ff59;
  border-radius: 5px;
}

.span1 {
  margin-bottom: 15px;
}

.span2 {
  margin-left: 70px;
}


.right-box {
  left: 20%;
  height: 100%;
  width: 80%;
  position: relative;
}

.moon {
  position: absolute;
  left: 40.5% !important;
  top: 26% !important;
  z-index: 3;
}

.mini-moon {
  fill: red;
  position: absolute;
  width: 30%;
  height: 30%;
  left: 60% !important;
  top: 15% !important;
}

.cloud1 {
  left: 28% !important;
  top: 60% !important;
  z-index: 4;
}

.cloud2 {
  left: 60% !important;
  top: 38% !important;
  z-index: 4;
}

.cloud3 {
  left: 65% !important;
  top: 68% !important;
  z-index: 4;
}

.bcloud1 {
  left: 60% !important;
  top: 10% !important;
  z-index: 1;
}

.bcloud2 {
  left: 30% !important;
  top: 20% !important;
  z-index: 1;
}
